import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { Table } from '../../../components';
import { getAllVendorsOfPromoterSaga } from '../../../store/actions';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

// eslint-disable-next-line no-unused-vars
const Chefs = props => {
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);

  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };

  const dispatch = useDispatch();

  const {
    chefs: { list: allData, totalRecords },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.chef);
  const { open } = useSelector(state => state.modal);

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllVendorsOfPromoterSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
        }),
      );
    } else {
      dispatch(
        getAllVendorsOfPromoterSaga({
          columnName,
          orderBy,
          search,
          skip,
          limit,
        }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  const tableFields = [
    {
      displayName: 'BrVendorand Name',
      key: 'restaurantName',
    },
    {
      displayName: 'Full Name',
      key: 'name',
    },
    {
      displayName: 'Email',
      key: 'email',
    },
    {
      displayName: 'Phone Number',
      key: 'phone',
    },

    {
      displayName: 'Vendor Rating',
      key: 'avgRating',
      className: 'active-green',
      isRating: true,
      ratingType: 'star',
    },
    {
      displayName: 'Status',
      key: 'isApproved',
      isConditional: true,
      positiveValue: 'Approved',
      negativeValue: 'Not Approved',
    },
    {
      displayName: 'Commission(%)',
      key: 'commissionPercentage',
      noSort: true,
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: '/promoter/vendor-detail',
        },
      ],
    },
  ];

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Vendors </h2>
              </div>
              <div className="text-right  d-flex-right">
                <Link
                  to="/promoters/add-vendor"
                  className="btn btn-primary m-l-sm m-b-md"
                >
                  Add Vendor
                </Link>
                {/* <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  data-toggle="modal"
                  data-target="#send-modal"
                  onClick={() => {
                    setEditCategory(false);
                    setModelOpen(true);
                  }}
                >
                  Add Promoter
                </button> */}
              </div>
              <div className="coman-table" id="owner-detail-btn2">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  // link={linkObj}
                  linkPropsName="_id"
                  // statusAction={statusAction}
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  // pageSizeProp={pageSize}
                />
              </div>
            </div>
          </div>
        </div>
        {open && <AlertMessageModal />}
      </div>
    </div>
  );
};

export default SetTokenHeader(Chefs, axios);
